.box {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  width: 450px;
  height: 100px;
  background-color: red;

  margin: auto;
}

.box > p {
  color: white;
  margin-bottom: 1px;
}

.center-content {
    display: flex;
    justify-content: center;
}

.model-figure-container {
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.model-figure-container > * {
    margin: 50px 50px;
}

.fig-container {
    height: 600px;
}

.fig-container > img {
    display: block;
    height: 100%;
}

.lake-condition-container {
    position: relative;
    display: flex;
    justify-content: center;
    font-size: 14px;
    align-items: center;
}

.lake-condition-left-column {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}



/* Lake Condition Block */

.lake-condition-description-container {
    display: flex;
    flex-direction: column;
    margin-right: 50px;
}

.lake-condition-title {
    font-size: 35px;
    font-weight: 400;
}

.lake-condition-description {
    margin-top: 20px;
    max-width: 500px;
    line-height: 1.3em;
}

.lake-visual-container {
    display: flex;
    flex-direction: row;
    max-height: 90vh;
}


/* Lake Forecast Information */

.lake-condition-info {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    color: black;
    border-radius: 20px;
    box-shadow: 3px 3px 10px #dedede;
    transition: box-shadow 0.2s linear;
    padding: 20px;
}

.lake-condition-info:hover {
    cursor: pointer;
    box-shadow: 3px 3px 40px #dedede;
}

.lake-condition-info-active {
    box-shadow: 3px 3px 40px #dedede;
}

.lake-condition-date {
    font-size: 20px;
}

.lake-condition-value {
    margin-top: 5px;
}

.lake-condition-controls {
    display: flex;
    flex-direction: row;
    margin-top: 15px;
}
